</section></template>

<script>
import ElDataTable from 'el-data-table'

export default {
  name:'common-data-table',
  components: {ElDataTable},
  data: function() {
    return {
      form: [
        {
          $type: "input",
          $id: "title",
          label: "",
          rules: [
            {
              required: true,
              message: "",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "" }
        },
        {
          $type: "input",
          $id: "tab",
          label: "",
          rules: [
            {
              required: true,
              message: "",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "" }
        },
        {
          $type: "input",
          $id: "content",
          label: "",
          rules: [
            {
              required: true,
              message: "",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: {
            placeholder: "",
            type: "textarea",
            rows: 3
          }
        }
      ],
      columns: [
        { prop: "id", label: "ID" },
        { prop: "title", label: "" },
        { prop: "tab", label: "", width: 100 }
      ],
      searchForm:[
        {
          $type: "input",
          $id: "tab",
          label: "",
          rules: [
            {
              required: true,
              message: "",
              trigger: "blur",
              transform: v => v && v.trim()
            }
          ],
          $el: { placeholder: "" }
        }
      ]
    }
  }
}
</script>


